@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: 'Gaegu', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:"Gaegu",sans-serif;background-color:#e2ded8;color:#9f988c;width:100vw;height:100vh;display:grid;place-items:center}.App .info{text-align:center;margin:1rem;-webkit-user-select:none;-ms-user-select:none;user-select:none}.App .info h1{font-weight:bold;font-size:32px}.App .info p{font-size:24px}
